.button__light {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 45%;
    margin: 0 auto;
    padding: 10px 30px;
    cursor: pointer;
    color: #000;
    font-family: 'Montserrat Alternates', sans-serif;
    font-weight: 500;
    font-size: 14px;
    transition: all 0.3s ease 0s;
    border-radius: 8px;
    border: 2px solid #000;
    background: #ffffff;
    box-shadow: 2px 2px 0px 0px #CD4631;
}

.button__light:hover,
.button__light:focus {
    box-shadow: none;
}

.button__dark {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 60%;
    margin: 0 auto;
    padding: 10px 48px;
    cursor: pointer;
    font-family: 'Montserrat Alternates', sans-serif;
    font-weight: 500;
    font-size: 18px;
    transition: all 0.3s ease 0s;
    border-radius: 8px;
    color: #F7EDE8;
    background: #000;
    box-shadow: 4px 4px 0px 0px rgba(0, 0, 0, 0.25);
}

.button__dark:hover,
.button__dark:focus {
    box-shadow: none;
}

.form {
    display: flex;
    flex-direction: column;
    /* align-items: center; */
    width: 420px;
    margin: 0 auto;
    padding: 36px 26px;

    border-radius: 12px;
    border: 2px solid #000;
    background: #FFF;    
    margin-bottom: 35px;
    box-shadow: 6px 6px 0px 0px #81ADC8;
}

.form__label {
    margin: 0 22px 12px 22px;
    font-size: 16px;
    font-weight: 500;
}

.form__input {
    display: block;
    margin: 0 20px 22px 20px;
    padding: 12px 22px;
    outline: 0;
    font-family: 'Montserrat Alternates', sans-serif;
    font-size: 18px;
    color: #000000;
    border-radius: 8px;
    border: 1.5px solid #000;
    transition: all 0.3s ease 0s;
}

.form__input:hover,
.form__input:focus {
    transition: all 0.3s ease 0s;
    box-shadow: 4px 4px 0px 0px rgba(0, 0, 0, 0.25);
}